export class ResponseToolbarBasic {
    id?: string; // æ è®°
    class?: string; // class
    icon?: string; // å¾æ å
    groupId?: string; // æå±çç»,åªæä¸å±åæ
    order?: number; // æ¶èé¡ºåº
    asDropDownTop?: boolean; // æ¯å¦ä½ä¸ºä¸æèåçé¡¶å±æé®
    text: string; // ææ¬
    isDP: boolean;
    disabled: boolean;//æ¯å¦ç¦ç¨
    visible: boolean;//æ¯å¦å¯è§
    responsed: boolean;    
    tipsEnable?:boolean;//æ¯å¦å¯ç¨æç¤ºæ¶æ¯
    tipsText?:string;//æç¤ºæ¶æ¯åå®¹
    width: Number; // è®°å½å®½åº¦
    constructor(item: any) {
        const defaultData = {
            id: '',
            class: 'btn-secondary',
            icon: '',
            groupId: '',
            order: -1,
            asDropDownTop: false,
            text: '',
            isDP: false,
            disabled: false,
            visible: true,
            responsed: false,
            width: 0,
            tipsEnable:false,
            tipsText:''
        };
        Object.keys(defaultData).forEach(propName => {
            this[propName] = item.hasOwnProperty(propName)
                ? item[propName]
                : defaultData[propName];
        });
    }
    // è®¾ç½®å®½åº¦
    setWidth(value: string) {
        this.width = parseInt(value, 10);
    }
    // è·åå®½åº¦
    getWidth(): any {
        if(this.visible){
            return this.width;
        }
        return false;
    }
}

