export class ResponseToolbarDropDown extends ResponseToolbarBasic {
    placement?: string; // ä¸æä½ç½®
    topItem?: ResponseToolbarItem | null;
    dropdownCls: string; // ä¸æclass
    menuCls: string; // ä¸æèåclass
    split: boolean; // æ¯å¦ç¨åå¼çä¸ææé®
    children: Array<ResponseToolbarItem | ResponseToolbarDropDown>;
    constructor(item) {
        super(item);
        const defaultData = {
            topItem: null,
            isDP: true,
            children: [],
            class: 'btn-secondary',
            dropdownCls: '',
            menuCls: '',
            placement: '',
            split: false
        };

        Object.keys(defaultData).forEach(propName => {
            this[propName] = item.hasOwnProperty(propName)
                ? item[propName]
                : defaultData[propName];
        });
    }
    /**
     * æ¯å¦æå­é¡¹
     */
    hasChild(): boolean {
        return this.children.length > 0 ? true : false;
    }
    /**
     * è¿½å å­é¡¹,é»è®¤ä»å¼å¤´æå¥
     * @param item
     */
    addChild(item) {
        this.children.unshift(item);
    }
    // ä»å¤´é¨å¼å§å é¤å­åç´ 
    removeChild(){
        this.children.shift();
    }
    // // è®¾ç½®å®½åº¦
    // setWidth(value: string) {
    //   this.width = parseInt(value, 10);
    // }
    // // è·åå®½åº¦
    // getWidth(): any {
    //   return this.width;
    // }
}

export class ResponseToolbarGroup {
    id: string;
    name: string;
    presetId: Array<string> = []; // è®°å½åç´ çId
    responsedIndex: Array<Number> = []; // è®°å½è½¬åä¸ºä¸ææ¶åç´ çä½ç½®
    width: number; // è®°å½å®½åº¦
    constructor(_id: string, _groupName: string) {
        this.id = _id;
        this.name = _groupName;
        this.width = 0;
    }
    // è®¾ç½®å®½åº¦
    setWidth(value: any) {
        this.width = parseInt(value + '', 10);
    }
    // è·åå®½åº¦
    getWidth(): any {
        return this.width;
    }
    // æ´æ°presetIndex
    setPreset(value: Array<string> | string) {
        if (Array.isArray(value)) {
            this.presetId = this.presetId.concat(value);
        } else {
            this.presetId.push(value);
        }
    }
    // æ¸é¤preset
    delPreset(){
        this.presetId=[];
    }
    // å é¤
    removeResponsed(index) {
        this.responsedIndex.splice(index, 1);
    }
    // æ´æ°responsedIndex
    setResponsed(value: Array<Number> | Number) {
        if (Array.isArray(value)) {
            this.responsedIndex = this.responsedIndex.concat(value);
        } else {
            this.responsedIndex.push(value);
        }
    }
    // æ¯å¦å·²ç»å¼å§å¤çååºå¼
    isResponsing() {
        return this.responsedIndex.length > 0;
    }
    // æ¯å¦å·²ç»å¤çå®ååºå¼
    isResponsed() {
        return this.presetId.length === this.responsedIndex.length;
    }
}

