import { throttleTime, debounceTime } from "rxjs/operators";
import { LocaleService } from "@farris/ui-locale";
import ResizeObserver from "resize-observer-polyfill";
import { FDropdownDirective } from "@farris/ui-dropdown";
@Component({
    selector: "f-response-toolbar",
    templateUrl: "./response-toolbar.component.html",
    styleUrls: ["./response-toolbar.component.css"],
   // changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResponseToolbarComponent
    implements OnInit, AfterViewInit, DoCheck, AfterViewChecked, OnDestroy {
    @HostBinding("class.f-response-toolbar") frtCls = true;
    @HostBinding("class.position-relative") prCls = true;
    // ååºå¼å®¹å¨
    @ViewChild("resizedContainer") resizedContainerEl;
    // è¾å©è®¡ç®å®¹å¨
    @ViewChild("auxiliaryContainer") auxiliaryContainerEl;
    // ååºå¼åå®¹å®¹å¨
    @ViewChild("resizedContent") resizedContentEl;
    //
    @ViewChild("resizedContainer", { read: FResizedDirective })
    resizedContainerDirective: FResizedDirective;
    // ä¼ éç¹å»äºä»¶
    @Output()
    rtClickEvent: EventEmitter<ResponseToolbarClickEvent> = new EventEmitter<
        ResponseToolbarClickEvent
    >();
    // è¾å©è®¡ç®å®½åº¦æ°æ®
    auxiliaryDatas: Array<ResponseToolbarDropDown> = [];
    // æ§å¶ç¶åç´ æ»å¨çæ ·å¼
    //  private _dpState = false;
    //
    private ro: ResizeObserver | null = null;
    // è®°å½å®¹å¨å®½åº¦
    private containerWidthRecord = 0;
    // è®°å½åå
    private needResponseChanges = 0;
    referEl = null;
    dpState = [false];
    // ç¨æ¥åé¨è®°å½
    private _auxiliary = -1;
    private _selfEl: HTMLElement;
    resizeGroups: Array<ResponseToolbarGroup> = [];
    // è®°å½æ ¼å¼ååçæ°æ®
    toolbarDatas: Array<ResponseToolbarItem | ResponseToolbarDropDown> = [];
    // åé¨è®°å½æ ¼å¼ååçåå§æ°æ®-------------------------ææ¶ä¸ç¨
    // _toolbarDatas: Array<ResponseToolbarItem | ResponseToolbarDropDown> = [];
    @Input()
    set groups(values: Array<ResponseToolbarGroup>) {
        if (values) {
            this.resizeGroups = [];
            values.forEach(group => {
                this.resizeGroups.push(
                    new ResponseToolbarGroup(group.id, group.name)
                );
                // è¿æ²¡æè®¡ç®é¢ç½®æåº
            });
        }
    }
    private distanceThreshold = 20;
    private isUpdateTBView = false;

    @Input()
    set datas(values: Array<ResponseToolbarItem | ResponseToolbarDropDown>) {
        if (values) {
            this.toolbarDatas = this._formatData(values);
            this.needResponseChanges++;
            // å¤çåç»æ°æ®
            this._reOrganizeResizeGroups();
        }
    }
    _stateDatas: Observable<Array<any>> = new Subject();
    // æé®çç¦ç¨ç¶æ
    @Input()
    set btnState(values: Observable<Array<any>>) {
        this._stateDatas = values;
    }
    _btnVisibleDatas: Observable<Array<any>> = new Subject();
    // æé®çæ¥çç¶æ
    @Input()
    set btnVisible(values: Observable<Array<any>>) {
        this._btnVisibleDatas = values;
    }
    @Input() clickThrottleTime = 350;
    private clickItems = new Subject<any>();
    private clickSubscription: Subscription;
    private rtSize = "";
    /*å°ºå¯¸ï¼æsm,lg,ç©ºå¼ï¼é»è®¤æ¯ç©ºå¼ */
    @Input()
    set buttonSize(btnSize: string) {
        if (btnSize != this.rtSize) {
            this.rtSize &&
                this.render.removeClass(
                    this._selfEl,
                    "f-response-toolbar-" + this.rtSize
                );
            this.rtSize = btnSize;
            this.rtSize &&
                this.render.addClass(
                    this._selfEl,
                    "f-response-toolbar-" + this.rtSize
                );
        }
    }
    get buttonSize(): string {
        return this.rtSize;
    }
    /*å¼ºå¶æ¹åï¼é»è®¤ä¸ºç©ºå¼ï¼å¯ä»¥ætopãbottomï¼æä¸ãæä¸ã
     * ä½¿ç¨åºæ¯ï¼å·¥å·æ åé¨çä¸æï¼éè¦æä¸æèéè¦æä¸å±å¼ï¼ç»ä¸æ§å¶
     */
    @Input() popDirection = "";
    // rightæé®é å³ leftæé®é å·¦
    @Input() btnAlign = "right";
    private ngZone = null;
    constructor(
        el: ElementRef,
        private render: Renderer2,
        private cd: ChangeDetectorRef,
        public localeService: LocaleService
    ) // private ngZone: NgZone
    {
        this._selfEl = el.nativeElement;
        if (el.nativeElement.parentElement) {
            this.referEl = el.nativeElement.parentElement;
        }
    }
    // å ä¸ºæ§æ¨¡æ¿å¸¦æè¿ä¸ªæ¹æ³
    ngAfterViewInit() {}
    // å ä¸ºæ§æ¨¡æ¿å¸¦æè¿ä¸ªæ¹æ³
    ngDoCheck() {}
    ngOnInit() {
        // å»æoverflowä¹å
        this.render.addClass(
            this.auxiliaryContainerEl.nativeElement,
            "response-toolbar-hidden-element"
        );
        // æé®çç¦ç¨ç¶æ
        this._stateDatas.subscribe(data => {
            this.changeState(data);
        });
        // æé®çå¯è§ç¶æ
        this._btnVisibleDatas.subscribe(data => {
            this.changeVisible(data);
        });
        this.observerElement();
        //å¤çè¡¨åå è½½åçæé®åå
        this.ngZone.runOutsideAngular(() => {
            let selfObj=this;
            setTimeout(()=>{
                selfObj.responseResize();
            },0);
        });
        // æ¦æªç¹å»äºä»¶åªä¼ éç¬¬ä¸æ¬¡ç¹å»äºä»¶çå¤çæä½äº¤ç»parentæ¥å¤ç
        this.clickSubscription = this.clickItems
            .pipe(debounceTime(this.clickThrottleTime))
            .subscribe((eventDatas: ResponseToolbarClickEvent) => {
                this.rtClickEvent.emit(eventDatas);
            });
        
        if (this.popDirection === 'default') {
            this.popDirection = '';
        }
    }
    ngOnDestroy() {
        if (this.ro) {
            this.ro.unobserve(this.resizedContainerEl.nativeElement);
            this.ro = null;
        }
        if (this.clickSubscription) {
            this.clickSubscription.unsubscribe();
        }
    }
    // è¿ä¸ªæ¹æ³ææ§è½é®é¢ï¼å¾åæå¤ç TODO
    ngAfterViewChecked() {
        if (this.needResponseChanges > 0) {
                this.needResponseChanges--;
                this.responseResize();
        }else if (this.auxiliaryDatas.length>0) {
                this._calculate(
                    true,
                    this.auxiliaryContainerEl.nativeElement.offsetWidth
                );
            }
    }
    // å ä¸ºæ§æ¨¡æ¿å¸¦æè¿ä¸ªæ¹æ³
    onResize(event) {}
    /**
     * å¼å®¹æ§æ¨¡æ¿æ§æ¹æ³
     * å¨ä¸å­å¨ngZoneçæåµä¸ï¼åå»ºngZone,ç¶åç»å®äºä»¶
     */
    private observerElement() {
        if (!this.ngZone) {
            const moduleInjector = this.cd["_view"].root.ngModule.injector;
            this.ngZone = moduleInjector.get<NgZone>(NgZone, null);
        }

        this.ngZone.runOutsideAngular(() => {
            this.ro = new ResizeObserver((entries, observer) => {
                const tempWidth = entries[0].contentRect.width;
                if (
                    Math.abs(tempWidth - this.containerWidthRecord) >
                        this.distanceThreshold
                ) {
                    this.responseResize();
                    this.containerWidthRecord = tempWidth;
                }
            });
            this.ro.observe(this.resizedContainerEl.nativeElement);
        });
    }
    /**
     * å¼ºå¶è½¬æ¢æ¹å
     * @param selfDefinePL
     * @param defaultPL
     */
    getPlacement(selfDefinePL: string, defaultPL: string): string {
        let oldDirection = selfDefinePL ? selfDefinePL : defaultPL;
        if (!this.popDirection) {
            return oldDirection;
        }
        var plRelation = { bottom: "top", top: "bottom" };
        oldDirection = oldDirection == "left" ? "left-bottom" : oldDirection;
        oldDirection = oldDirection == "right" ? "right-bottom" : oldDirection;
        let newDirection = oldDirection.replace(
            plRelation[this.popDirection],
            this.popDirection
        );
        return newDirection;
    }

    /**
     * è°ç¨æ­¤æ¹æ³çåºæ¯
     * 1ãçé¢ææ½èªå¨è§¦å
     * 2ãæ¾ç¤ºè°ç¨
     */
    responseResize() {
        const _distance = this._getDistance();
        if (_distance > 0) {
            // å¦æåç»é½å·²ç»å¤çå®,ååºç°æ»å¨æ¡ä¸ç®¡
            const _tempIndex = this._getFirstUnResponsedIndex();
            if (_tempIndex < 0) {
                return;
            }
            // ææç¬¬ä¸å±ä¸æé½åæ
            this.dpState = [false];
            for (let k = _tempIndex; k < this.resizeGroups.length; k++) {
                const _tempGroup = this.resizeGroups[k];
                this._auxiliary = k;
                if (_tempGroup.isResponsing()) {
                    this._calculate();
                } else {
                    // å¦æè¿æªå¼å§å¤çååºå¼
                    const _tempDP = new ResponseToolbarDropDown({
                        text: _tempGroup.name,
                        id: _tempGroup.id,
                        placement: this.popDirection
                            ? this.popDirection + "-left"
                            : "bottom-left",
                        width: _tempGroup.getWidth()
                    });
                    // å·²ç»å¤çè¿å®½åº¦
                    if (_tempDP.getWidth()) {
                        this.auxiliaryDatas = [_tempDP];
                        this._calculate(true, _tempDP.getWidth());
                    } else {
                        // æ´æ°è¾å©æ°æ®
                        let auxiliaryIndex=this.auxiliaryDatas.findIndex((item)=>{
                            return item.id==_tempDP.id
                        });
                        if(auxiliaryIndex==-1){
                            this.auxiliaryDatas.push(_tempDP);
                        }
                        if(this.cd&&!(this.cd as ViewRef).destroyed){
                            this.cd.detectChanges();
                        }
                    }
                }
            }
        } else {
            // å¯»æ¾æåä¸ä¸ªå¤çååºå¼çåç´ 
            const _tempIndex = this._getLastResponseIndex();
            if (_tempIndex < 0) {
                return;
            }
            // ä¸´æ¶å­å¨æ°æ®
            let _tempToolbarDatas = [].concat(this.toolbarDatas);
            // å¦æå·²ç»å¼å§å¤çååºå¼
            for (let k = _tempIndex; k >= 0; k--) {
                const result = this._restitute(_tempToolbarDatas, k);
                _tempToolbarDatas = [].concat(result["data"]);
                if (!result["continueTo"]) {
                    break;
                }
            }
            // æ´æ°æ°æ®
            this.toolbarDatas = [].concat(_tempToolbarDatas);
            // ææç¬¬ä¸å±ä¸æé½åæ
            this.dpState = [false];
            if(this.cd&&!(this.cd as ViewRef).destroyed){
                this.cd.detectChanges();
            }
        }
    }
    // ä¸æçå±å¼æ¶èµ·ç¶æ
    dpSectionState(state: boolean, id) {
        if (state) {
            // å±å¼ç¶æä¸
            let dropdowns = this.resizedContentEl.nativeElement.querySelectorAll(
                "[fDropdown]"
            );
            if (dropdowns && dropdowns.length > 1) {
                for (var k = 0; k < dropdowns.length; k++) {
                    if (
                        dropdowns[k].className.indexOf("show") &&
                        dropdowns[k].id != id
                    ) {
                        // dropdowns[k].dispatchEvent(new Event('selfClose'));
                        this.compatibleDispatchEvent(dropdowns[k], "selfClose");
                    }
                }
            }
        }
    }
    private compatibleDispatchEvent(eventEl, eventName) {
        var event;
        if (typeof Event === "function") {
            event = new Event(eventName);
        } else {
            event = document.createEvent("Event");
            event.initEvent(eventName, false, false);
        }
        eventEl.dispatchEvent(event);
    }
    // ä¿®æ¹å¯ç¨ç¦ç¨ç¶æ
    changeState(values) {
        const idArray = Object.keys(values);
        idArray.forEach(id => {
            const state = values[id];
            const item = this._findItemByID(id, this.toolbarDatas);
            if(item){
                 item["disabled"] = state;
            }
        });
        if(this.cd&&!(this.cd as ViewRef).destroyed){
            this.cd.detectChanges();
        }
    }
    /**
     * ä¿®æ¹æ¾ç¤ºç¶æ
     * å¦ææ¯ä¸ææé®
     *   å¤æ­ä¸é¢çå­æ¯å¦é½éè
     * @param values
     */
    changeVisible(values) {
        const idArray = Object.keys(values);
        idArray.forEach(id => {
            const visible = values[id];
            const item = this._findItemByID(id, this.toolbarDatas);
            if(item){
                item["visible"] = visible;
            }
        });
        // éåå¾ªç¯,æ´æ°æ°æ®
        this.toolbarDatas.forEach(item => {
            // å¦ææ¯ä¸æ
            if (item["isDP"]) {
                this._checkDropdownVisible(values, item);
            }
        });
        this.needResponseChanges++;
        // æ´æ°
       //this.cd.markForCheck();
       if(this.cd&&!(this.cd as ViewRef).destroyed){
        this.cd.detectChanges();
     }
    }
    /**
     * å¦ææ¯ä¸ææé®
     *   å¤æ­ä¸é¢çå­æ¯å¦é½éè
     *       A. å¦æå­é½éèï¼é£ä¹æ´ä¸ªä¸æé½è¢«éèï¼
     *       B. å¦æå­å¨å­æ²¡æéèï¼èä¸ä¸ææ²¡æè¢«é¢ç½®éèï¼ä¸ææé®æ¾ç¤º
     * @param outsideVisibleArrays ä»ç»ä»¶å¤ä¼ å¥çå¯è§ç¶æå¯¹è±¡
     * @param dropdownItem ä¸æåç´ 
     */
    private _checkDropdownVisible(outsideVisibleArrays, dropdownItem): void {
        //å¦ææ²¡æè¢«å¼ºå¶è®¾ç½®ç¶æ
        if (!outsideVisibleArrays.hasOwnProperty(dropdownItem["id"])) {
            // ä¸æåç´ ä¸å¯è§ï¼å¤æ­æ¯å¦æå¯è§å­åç´ 
            if (
                !dropdownItem["visible"] &&
                this._hasVisibleItem(dropdownItem["children"], true)
            ) {
                dropdownItem["visible"] = true;
            }
            // ä¸æåç´ æ¯å¯è§ï¼å¤æ­æ¯å¦æå­åç´ é½ä¸å¯è§
            if (
                dropdownItem["visible"] &&
                !this._hasVisibleItem(dropdownItem["children"], true)
            ) {
                dropdownItem["visible"] = false;
            }
        }
        dropdownItem.children.forEach(item => {
            if (item["isDP"]) {
                this._checkDropdownVisible(outsideVisibleArrays, item);
            }
        });
    }
    /**
     * å¨å¾æ£æ¥æ°æ®ä¸­å¿ï¼æ£æ¥æ¯å¦å­å¨è¯¥å¯è§ç¶æçæ°æ®ã
     * å¦ææè¿åtrue,å¦åè¿åfalse
     * @param datas å¾æ£æ¥æ°æ®
     * @param visible å¯è§ç¶æ
     */
    private _hasVisibleItem(datas, visible): boolean {
        const findIndex = datas.findIndex(childItem => {
            if (childItem["visible"] == visible) {
                return true;
            }
            return false;
        });
        if (findIndex > -1) {
            return true;
        }
        return false;
    }

    /**
     * æè·ç¹å»
     * @param ev
     */
    clickItem(ev: MouseEvent, ddRef?: FDropdownDirective) {
        ev.stopPropagation();
        // å¦ææ¯åç¦»çä¸ææé®
        if (this.elhasSpecialCls(ev.target, "dropdown-toggle-split")) {
            ev.stopImmediatePropagation();
        }
        // å¤æ­æ¯å¦ç¹å»å°éè¦å¤çäºä»¶çåç´ ä¸,æé®ãä¸ææé®
        var clickEvEl = this.findBtnOrTogglerItemFromClick(ev.target);
        if (clickEvEl) {
            let tempID = clickEvEl["id"];
            // ä¿®æ¹æ¨¡æ¿ï¼idè°æ´å°ä¸ææ´ä½ä¸
            if (
                !tempID &&
                this.elhasSpecialCls(clickEvEl["parentNode"], "f-rt-dropdown")
            ) {
                tempID = clickEvEl["parentNode"].id;
            }
            if (tempID) {
                const tempItem = this._findItemByID(tempID, this.toolbarDatas);
                // å¦æåç´ ç¦ç¨
                if (tempItem["disabled"]) {
                    // ç¦æ­¢åä¸ä¼ é
                    ev.stopImmediatePropagation();
                } else {

                    this.clickItems.next({
                        id: tempID,
                        text: tempItem["text"],
                        hidden: tempItem["hidden"]
                    });

                    
                    if (ddRef) {
                        ddRef.close();
                    }
                }
            }
        } else {
            ev.stopImmediatePropagation();
        }
    }
    private findBtnOrTogglerItemFromClick(clickTarget) {
        // ç¶çº§æ ç­¾æ¯å¦æ¯body,æ¯çåæ­¢è¿åéå,åä¹ç»§ç»­
        if (this.elhasSpecialCls(clickTarget)) {
            return clickTarget;
        } else if (
            clickTarget.parentNode["className"]&&clickTarget.parentNode["className"].indexOf("f-response-content") < 0
        ) {
            var parentN = clickTarget.parentNode;
            if (this.elhasSpecialCls(parentN)) {
                return parentN;
            } else {
                return this.findBtnOrTogglerItemFromClick(parentN);
            }
        }
        // è¿åéå,ç»æ
        return null;
    }
    private elhasSpecialCls(el, clsName = "") {
        if (el) {
            var classNameList = el.classList;
            var findCls = false;
            for (var k = 0; k < classNameList.length; k++) {
                if (clsName) {
                    if (classNameList[k] == clsName) {
                        findCls = true;
                        break;
                    }
                } else {
                    if (
                        classNameList[k] == "f-rt-btn" ||
                        classNameList[k] == "f-rt-toggle"
                    ) {
                        findCls = true;
                        break;
                    }
                }
            }
            return findCls;
        }
        return false;
    }
    /**
     * æ ¹æ®ä¼ éçæ°æ®éæ°ç»å»ºåç»
     * ToDo ææ¶æ²¡ææ´å¤æçåç»ï¼å¦åéè¦è®°å½åç»
     */
    private _reOrganizeResizeGroups() {
        // å¤çåç»æ°æ®
        //  if (this.resizeGroups.length === 0) {
        let moreText = "æ´å¤";
        if (this.localeService) {
            moreText = this.localeService.getValue("responseToolbar.more");
        }
        const defaultResizeGroup = new ResponseToolbarGroup(
            "toolbar-group-1",
            moreText
        );
        for (let i = this.toolbarDatas.length - 1; i >= 0; i--) {
            defaultResizeGroup.setPreset(this.toolbarDatas[i]["id"]);
        }
        this.resizeGroups = [].concat(defaultResizeGroup);
       // this.cd.markForCheck();
       if(this.cd&&!(this.cd as ViewRef).destroyed){
        this.cd.detectChanges();
    }
    }
    /**
     * åå®¹ä¸å®¹å¨çå·®å¼
     */
    private _getDistance() {
        return (
            this.resizedContentEl.nativeElement.offsetWidth -
            this.resizedContainerEl.nativeElement.offsetWidth
        );
    }
    /**
     * æ ¹æ®è¿åç»æå¤æ­æ¯å¦ç»§ç»­
     * @param groupIndex
     */
    private _restitute(originalData, groupIndex) {
        const tempGroup = this.resizeGroups[groupIndex];
        // æ»å¨æ¡ååå®¹çè·ç¦»
        let tempDistance = this._getDistance();
        // è®¡ç®æåä¸ä¸ªä¸æçå®½åº¦
        const tempGroupData =
            originalData[this._findIndexByID(originalData, tempGroup.id)];
        // æ¾å°è¿åçä½ç½®
        const tempPresetIndex = [].concat(tempGroup.responsedIndex);
        // æé¤åä¸æå­åç´ è¢«è®¡ç®
        for (let j = tempPresetIndex.length - 1; j >= 0; j--) {
            if (tempDistance >= 0) {
                break;
            }
            // ä»ä¸æä¸­ç§»é¤
            const tempChildData = tempGroupData.children[0];
            const specialDistance = j === 0 ? tempGroupData.getWidth() : 0;

            tempDistance =
                tempDistance +
                (tempChildData.getWidth() ? tempChildData.getWidth() : 0) -
                specialDistance;

            if (tempDistance >= 0) {
                break;
            } else {
                // æ´æ°ä¸æ
                tempGroupData.removeChild();
                // æå¥å°åä½ç½®
                originalData.splice(tempPresetIndex[j], 0, tempChildData);
                // æ´æ°ä½ç½®
                tempGroup.removeResponsed(j);
                if (j === 0) {
                    originalData.splice(
                        this._findIndexByID(originalData, tempGroup.id),
                        1
                    );
                }
            }
        }
        return {
            continueTo: tempDistance > 0 ? true : false,
            data: originalData
        };
    }

    /**
     * å¼å§è®¡ç®
     * @param datas
     */
    private _calculate(auxiliary: boolean = false, tGroupWidth: number = 0) {
        if (this._auxiliary < 0) {
            return;
        }
        // è®°å½éè¦ä¿®æ¹çä¸æ
        let tempDP;
        // æ»å¨æ¡ååå®¹çè·ç¦»
        let tempDistance = this._getDistance() + tGroupWidth;
        // ä¸´æ¶å­å¨æ°æ®
        const tempToolbarDatas = [].concat(this.toolbarDatas);
        const tempGroup = this.resizeGroups[this._auxiliary];
        if (auxiliary) {
            tempDP = this.auxiliaryDatas.pop();
            tempDP.setWidth(tGroupWidth);
            tempGroup.setWidth(tGroupWidth);
        } else {
            tempDP =
                tempToolbarDatas[
                    this._findIndexByID(tempToolbarDatas, tempGroup.id)
                ];
        }
        // å½åå©ä½éè
        for (
            let j = tempGroup.responsedIndex.length;
            j < tempGroup.presetId.length;
            j++
        ) {
            if (tempDistance <= 0) {
                break;
            }

            // è®¡ç®å¨å½åæ°æ®ä¸­çä½ç½®ï¼æ ¹æ®è¿ä¸ªä½ç½®å¯ä»¥æ¾å®½åº¦
            const tempOldIndex = this._findIndexByID(
                this.toolbarDatas,
                tempGroup.presetId[j]
            ) as any;
            const tempNewIndex = this._findIndexByID(
                tempToolbarDatas,
                tempGroup.presetId[j]
            ) as any;

            // è®¡ç®æé®å®½åº¦
            let tempWidth = this.toolbarDatas[tempOldIndex].getWidth() as any;
            if (tempWidth === 0) {
                // æé®å®½åº¦æªæ¾è¢«è®¡ç®è¿
                tempWidth = this._getWidthFromElementsById(
                    tempGroup.presetId[j]
                );
                tempToolbarDatas[tempNewIndex].setWidth(tempWidth);
            } else if (tempWidth === false) {
                // æé®è¢«éè
                tempWidth = 0;
            }

            tempDP.addChild(tempToolbarDatas[tempNewIndex]);
            // ç§»é¤
            tempToolbarDatas.splice(tempNewIndex, 1);
            // ä¿å­ä½ç½®ç¨äºè¿å
            tempGroup.setResponsed(tempNewIndex);
            tempDistance = tempDistance - tempWidth;
        }
        if (auxiliary && tempDP.hasChild()) {
            // æå¥
            tempToolbarDatas.push(tempDP);
        }
        this._auxiliary = -1;
        this.toolbarDatas = [].concat(tempToolbarDatas);
        if(this.cd&&!(this.cd as ViewRef).destroyed){
            this.cd.detectChanges();
        }
    }

    /**
     * æ ¹æ®idæ¾å°å®éæ¾ç¤ºåç´ çå®½åº¦
     * @param findId
     */
    private _getWidthFromElementsById(findId): number {
        const parentEl = this.resizedContentEl.nativeElement;
        const childLen = parentEl.children.length;
        let result = 0;
        for (var k = 0; k < childLen; k++) {
            if (parentEl.children[k].id === findId) {
                let findEl=parentEl.children[k];
                let getComputedStyleEl=window.getComputedStyle(findEl,null);
                result = findEl.offsetWidth+parseFloat(getComputedStyleEl.marginLeft)+parseFloat(getComputedStyleEl.marginRight);
                break;
            }
        }
        return result;
    }
    /**
     * æ ¼å¼åæ°æ®
     */
    private _formatData(datas, idPrefix: string = "response") {
        const result = [];
        datas.forEach((item, index) => {
            if (item.isDP) {
                // å¦ææ¯ä¸æ
                if (
                    item.hasOwnProperty("children") &&
                    item["children"].length
                ) {
                    const children = [].concat(item["children"]);
                    // item.children = []; // åºgzqè¦æ±20230529
                    const dpItem = new ResponseToolbarDropDown(
                        Object.assign({ id: idPrefix + "_" + index }, item)
                    );
                    result.push(dpItem);
                    dpItem.children = this._formatData(
                        children,
                        idPrefix + "_" + index
                    );
                } else {
                    result.push(
                        new ResponseToolbarDropDown(
                            Object.assign({ id: idPrefix + "_" + index }, item)
                        )
                    );
                }
            } else {
                // éä¸æ
                result.push(
                    new ResponseToolbarItem(
                        Object.assign({ id: idPrefix + "_" + index }, item)
                    )
                );
            }
        });

        return result;
    }
    /**
     * æ¾å°ç¬¬ä¸ä¸ªæªå¤çå®ååºå¼çåç´ 
     */
    private _getFirstUnResponsedIndex() {
        const tempUnResponsedIndex = this.resizeGroups.findIndex(group => {
            if (group.isResponsed()) {
                return false;
            }
            return true;
        });
        return tempUnResponsedIndex;
    }
    /**
     * å¯»æ¾æåä¸ä¸ªå¤çååºå¼çåç´ 
     */
    private _getLastResponseIndex() {
        const tempResponsingIndex = this.resizeGroups.findIndex(group => {
            if (!group.isResponsing()) {
                return true;
            }
            return false;
        });
        // ææç»åç´ é½å·²å¤çå®ååºå¼
        if (tempResponsingIndex === -1) {
            return this.resizeGroups.length - 1;
        }
        // ææç»åç´ é½æªå¼å§å¤çååºå¼
        return tempResponsingIndex > 1 ? tempResponsingIndex - 1 : -1;
    }
    /**
     * æ¾å°groupå½¢æçä¸æï¼å¨æ°æ®ä¸­çä½ç½®
     */
    private _findIndexByID(
        datas: Array<ResponseToolbarItem | ResponseToolbarDropDown>,
        id: string
    ): any {
        const result = datas.findIndex((item, index) => {
            if (item["id"] === id) {
                return true;
            }
            return false;
        });
        return result;
    }
    /**
     * æ ¹æ®IDå¯»æ¾Item
     */
    private _findItemByID(
        id: string,
        data: Array<ResponseToolbarDropDown | ResponseToolbarItem>
    ): ResponseToolbarDropDown | ResponseToolbarItem {
        let tempResult = null;
        const tempIndex = data.findIndex((item, index) => {
            if (item["id"] === id) {
                tempResult = item;
                return true;
            }
            // å¦ææ¯ä¸æï¼å±çº§ä¸æ
            if (item["isDP"]) {
                const tempItem = this._findItemByID(id, item["children"]);
                if (tempItem) {
                    tempResult = tempItem;
                    return true;
                }
            }
            return false;
        });
        return tempResult;
    }
}
